<template>
  <!--브랜드 매장 안내-->
  <ul class="chatStore">
    <li class="csTits" v-for="(item, index) in data" :key="index">
      <a class="storeBtns"><b class="lineClamp3">{{ item.brandNm }}</b><em>{{ item.floor }}F / TEL : {{ item.phone }}</em></a>
      <!-- <a @click="window.cbMoveParent(item.url)" role="button" class="storeBtns"><b class="lineClamp3">{{ item.brandNm }}</b><em>{{ item.floor }}F</em></a> -->
      <a :href="`tel:${item.phone}`" @click="onClickPhone(item)" role="button" class="telBtns">문의하기</a>
      <a @click="onClickMap(item)" data-id="mapViewPopup" class="mapBtns layerPopupButton" aria-haspopup="true">지도보기</a>
    </li>
  </ul>
  <!--//브랜드 매장 안내-->
</template>

<style lang="scss">
.telBtns,
.mapBtns {
  cursor: pointer;
}
</style>

<script>
import consultingStatus from '@/mixins/consultingStatus';

export default {
  name: 'BrandStoreInfo',
  mixins: [consultingStatus],
  props: {
    data: Array,
  },
  mounted() {
    this.$uiHandler.popup.popupOn();
  },
  methods: {
    onClickPhone(item) {
      if (item.tags.phone) {
        this.$analyticsHandler.apply(item.tags.phone);
      }
    },
    onClickMap(item) {
      if (this.isOnConsulting) {
        return;
      }

      if (item.tags.mapUrl) {
        this.$analyticsHandler.apply(item.tags.mapUrl);
      }

      this.$store.commit('SHOW_MAP', item.mapUrl);
    },
  },
};
</script>